<template>
  <div :class="['portal-add-popup', checkBoxClass]">
    <el-dialog
      class="search"
      :title="addForm.title"
      :visible.sync="searchVisible"
      width="800px"
      :close-on-click-modal="false"
      :before-close="cancel"
    >
      <portal-form
        v-if="isShowSearch"
        ref="searchList"
        :form-data="formData"
        :form-config-list="formConfigList"
        :needOperateBtn="true"
        :form-rules="tempFormRules"
        :searchBtnLoading="searchLoading"
        :needBottomLine="true"
        :smallAutoColumn="smallAutoColumn"
        :bigAutoColumn="bigAutoColumn"
        @common-enter-event="search"
        @reset-btn-click="reset"
        @search-btn-click="search"
      />
      <table-page
        ref="data-table"
        size="small"
        :isSingleCheck="isSingleCheck"
        :is-check="isCheck"
        :is-check-memory="true"
        :is-search="refreshTableFlag"
        :has-pagination="hasPagination"
        :columns="addForm.columns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :init-checks="multipleChecks"
        :id-key="addForm.idKey"
        @fetchData="fetchData"
        @selection-change="onSelectionChange"
        @single-selection-change="singelSekectionChange"
      >
        <template
          v-for="item in addForm.columns.filter((val) => val.__slotName)"
          :slot="item.__slotName"
          slot-scope="scope"
        >
          <slot :name="item.__slotName" :row="scope.data.row"></slot>
        </template>
      </table-page>
      <div slot="footer">
        <div v-if="isShowCloseBtn">
          <el-button id="cancel" @click="cancel" size="small">{{ $t('common.button.close') }} </el-button>
        </div>
        <div v-else>
          <el-button id="cancel" @click="cancel" size="small">{{ $t('common.button.cancel') }} </el-button>
          <el-button id="confirm" @click="submit" size="small" type="primary">
            {{ $t('common.button.confirm') }}
          </el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import PortalForm from '@/components/portal-form';
import TablePage from '@/components/tablePage';

export default {
  name: 'addPrompt',
  components: { TablePage, PortalForm },
  props: {
    searchVisible: {
      type: Boolean,
      default: false,
    },
    hasCheckBox: {
      type: Boolean,
      default: true,
    },
    hasPagination: {
      type: Boolean,
      default: false,
    },
    isCheck: {
      type: Boolean,
      default: false,
    },
    isCheckMemory: {
      type: Boolean,
      default: false,
    },
    isShowCloseBtn: {
      type: Boolean,
      default: false,
    },
    tableData: {
      type: Array,
      default: () => [],
    },
    initChecks: {
      type: Array,
      default: () => [],
    },
    options: {
      type: Object,
      default: () => ({}),
    },
    addForm: {
      type: Object,
      default: () => {},
    },
    submitCallBack: {
      type: String,
      default: () => '',
    },
    childSearch: {
      type: Function,
      default: null,
    },
    smallAutoColumn: {
      type: Number,
      default: 2,
      validate: (value) => {
        return !(24 % value);
      },
    },
    bigAutoColumn: {
      type: Number,
      default: 2,
      validate: (value) => {
        return !(24 % value);
      },
    },
    // 是否显示搜索栏，不会影响现有页面
    isShowSearch: {
      type: Boolean,
      default: true,
    },
    isSingleCheck: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    searchVisible(val) {
      if (val) {
        this.refreshTableFlag = true;
        this.multipleSelection = this.getCheckedSelection();
      }
      this.firstLoadTableEnd = false;
    },
  },
  data() {
    return {
      tempFormRules: {
        propertyName: [{ required: false, message: 'Please enter', trigger: 'blur' }],
      },
      paginationConfig: {
        total: (this.addForm.columns && this.addForm.columns.length) || 0,
        pageIndex: 1,
        pageSize: 5,
        layout: 'total,prev, pager, next',
      },
      multipleSelection: [],
      multipleSelectionCache: {},
      uncommitSelectionache: {},
      refreshTableFlag: false,
      searchLoading: false,
      param: {},
      multipleChecks: this.initChecks,
      initChecksCache: this.initChecks,
      firstLoadTableEnd: false,
      singelSekectionChangeData: '',
    };
  },
  computed: {
    formConfigList() {
      return (
        (this.addForm.searchForm && this.addForm.searchForm.formConf) || [
          {
            label: this.addForm['label'],
            key: 'propertyName',
            prop: 'name',
            comp: 'el-input',
            bind: {
              clearable: true,
              placeholder: this.$t('common.placeholder.enter'),
            },
          },
        ]
      );
    },
    formData() {
      return this.addForm.searchForm && this.addForm.searchForm.formData;
    },
    checkBoxClass() {
      const hasCheckBox = this.hasCheckBox;
      if (hasCheckBox) {
        return 'checkbox-on';
      }
      return 'checkbox-off';
    },
  },
  methods: {
    singelSekectionChange(val) {
      this.singelSekectionChangeData = val;
    },
    getCheckedSelection() {
      const idKey = this.addForm.idKey || 'id';
      const ids = this.initChecks.map((item) => item[idKey]);
      const rows = this.getObjFlatValue(this.multipleSelectionCache, idKey);
      if (!rows) return [];
      return rows.filter((row) => ids.includes(row[idKey]));
    },
    fetchData(data) {
      this.paginationConfig.total = data.total;
      this.searchLoading = false;
      this.$emit('data-fetcher', data);
    },
    filterListByField(list, field) {
      return list.reduce((pre, next) => {
        if (!pre.length) {
          return [...pre, next];
        }
        const hasVal = pre.some((row) => row[field] === next[field]);
        if (hasVal) {
          return pre;
        }
        return [...pre, next];
      }, []);
    },
    getObjFlatValue(obj, idKey) {
      let arr = Object.values(obj);
      arr = arr.reduce((pre, next) => {
        if (!pre.length) {
          return [...next];
        }
        return [...pre, ...next];
      }, []);
      arr = this.filterListByField(arr, idKey);
      return arr;
    },
    onSelectionChange(val) {
      const idKey = this.addForm.idKey || 'id';
      const curPageNum = this.paginationConfig.pageIndex;
      let initChecksCache = this.initChecksCache;
      let curPageSelection = this.multipleSelectionCache[curPageNum] || [];
      // 当前 selection idKey list
      const ids = val.map((item) => item[idKey]);
      // 需要从缓存当前页的selection中删除项
      const deletedRows = curPageSelection.filter((item) => !ids.includes(item[idKey]));
      const deletedRowsIds = deletedRows.map((item) => item[idKey]);
      // 把 deletedRows 从 initChecksCache 中移除
      initChecksCache = initChecksCache.filter((item) => !deletedRowsIds.includes(item[idKey]));

      this.initChecksCache = [...initChecksCache];
      this.multipleSelectionCache[curPageNum] = [...val];
      // 把 multipleSelectionCache 转化成数组 multipleSelection，待submit提交使用
      // {1:[{a:'a'}],2:[{b:'b'}]} => [{a:'a'},{b:'b'}]
      this.multipleSelection = this.getObjFlatValue(this.multipleSelectionCache, idKey);
      // 未提交选中项，切页缓存
      this.uncommitSelectionache = { ...this.multipleSelectionCache };
    },
    async getTableData(param) {
      let result = null;
      const idKey = this.addForm.idKey || 'id';
      if (this.tableData.length > 0) {
        result = this.tableData;
      } else {
        if (this.param.searchKey) {
          param.searchKey = this.param.searchKey;
        }
        result = await this.childSearch(param);
      }
      // 未提交选中项数组，跟 selection 数据结构一致
      const uncommitSelectionacheArr = this.getObjFlatValue(this.uncommitSelectionache, idKey);
      // multipleChecks
      const initChecksCache = this.firstLoadTableEnd ? this.initChecksCache : this.initChecks;
      this.firstLoadTableEnd = true;
      const allCheckedSelection = [...initChecksCache, ...uncommitSelectionacheArr];
      const checkedIds = allCheckedSelection.map((check) => check[idKey]);
      const resList = result.list || [];
      // 判断tableList 是否包含选择项
      const hasInterSection = resList.some((row) => checkedIds.includes(row[idKey]));
      let multipleChecks = [];
      if (hasInterSection) {
        multipleChecks = allCheckedSelection;
      }
      // 等待空闲时间渲染checkbox选中
      window.requestIdleCallback(() => {
        this.multipleChecks = multipleChecks;
      });

      this.refreshTableFlag = false;
      return result;
    },
    search(key) {
      this.param.pageNo = 1;
      this.param.pageSize = 10;
      this.param.searchKey = key;
      this.searchLoading = true;
      this.$emit('update:search', this.param);
      try {
        this.$refs['data-table'].queryData(true);
      } finally {
        this.searchLoading = false;
      }
    },
    reset() {
      if (this.formData) {
        for (let key in this.formData) {
          this.formData[key] = null;
        }
      }
      this.refreshTableFlag = true;
      this.firstLoadTableEnd = false;
    },
    submit() {
      const options = this.options;
      // temporary
      if (this.isSingleCheck) {
        this.multipleSelection = this.singelSekectionChangeData;
      }
      this.$emit('submit-call-back', this.multipleSelection, options);
      this.$emit('update:searchVisible', false);
      this.multipleChecks = [];
    },
    cancel() {
      this.$emit('update:searchVisible', false);
      this.multipleChecks = [];
    },
  },
  created() {},
  mounted() {},
  activated() {},
};
</script>

<style lang="scss" scoped>
.checkbox-off {
  --checkbox-head-offset: 30px;
}
.checkbox-on {
  --checkbox-head-offset: 0;
}
.portal-add-popup {
  /deep/ .table-region .el-table__header .has-gutter .cell {
    padding-left: 0 !important;
  }
  /deep/ .has-gutter th {
    padding-left: 16px !important;
  }
  /deep/ .has-gutter th.el-table-column--selection {
    padding-left: var(--checkbox-head-offset) !important;
  }
  .table-page-container {
    margin-top: 20px;
    margin-bottom: 20px;
    /deep/ .table-region.el-table-column--selection .cell {
      padding-left: 0 !important;
    }
  }
  /deep/ .table-page-container .table-region .el-table-column--selection .cell {
    padding-left: 0 !important;
  }
}
/deep/ .form-flex-container {
  padding: 0 !important;
}
/deep/ .table-page-container .table-region .el-table .cell {
  position: relative;
}

/deep/ .el-checkbox__input {
  position: absolute;
  left: 0;
  top: -12px;
}

/deep/ .el-table__header-wrapper table th:first-child div.cell::after {
  display: none !important;
}
/deep/ .el-table__body {
  margin-bottom: 0 !important;
}
/deep/ .form-flex-container .col-flex-button-list {
  box-sizing: border-box;
}
.el-button--primary {
  color: $--text_color--white !important;
  background-color: $--primary_color !important;
  border-color: $--primary_color !important;
}
</style>
