<template>
  <el-dialog
    :title="$t('IDC.till.editStatus')"
    :visible="showEditStatusDialog"
    width="448px"
    class="dialog-container"
    :close-on-click-modal="false"
    @close="closeEditLanguageDialog()"
  >
    <el-form
      ref="editStatusFormRef"
      :model="editStatusForm"
      :rules="editStatusFormRules"
      label-position="top"
      label-width="200px"
    >
      <el-form-item prop="currentStatus" :label="$t('IDC.till.currentStatus')">
        <div class="history-item">{{ this.showStatus(editStatusForm.currentStatus) }}</div>
      </el-form-item>
      <el-form-item class="from-item-title" prop="newStatus" :label="$t('IDC.till.newStatus')">
        <el-select
          id="newStatus"
          v-model="editStatusForm.newStatus"
          :placeholder="$t('common.placeholder.select')"
          size="small"
        >
          <el-option v-for="item in newStatusOptions" :key="item.status" :label="item.value" :value="item.status">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="from-item-title" prop="remark" :label="$t('IDC.till.remark')">
        <el-input
          v-model.trim="editStatusForm.remark"
          clearable
          type="textarea"
          :rows="4"
          resize="none"
          :placeholder="$t('common.placeholder.enter')"
        >
        </el-input>
      </el-form-item>
    </el-form>
    <div slot="footer">
      <el-button id="cancel" @click="closeEditLanguageDialog()" size="small">{{
        $t('common.button.cancel')
      }}</el-button>
      <el-button
        :loading="submitLoading"
        id="confirm"
        type="primary"
        @click="submitDialogForm()"
        size="small"
        v-preventReClick
        >{{ $t('common.button.submit') }}</el-button
      >
    </div>
  </el-dialog>
</template>
<script>
import { ediStatus } from '@/views/identifier-center/api/api-till';
import { inputRequiredValidateObj, selectRequiredValidateObj } from '@/common/validator/common';
export default {
  name: 'editStatus',
  data() {
    return {
      submitLoading: false,
      showEditStatusDialog: false,
      editStatusForm: {
        currentStatus: '',
        newStatus: '',
        remark: '',
      },
      editStatusFormRules: {
        newStatus: [selectRequiredValidateObj],
        remark: [inputRequiredValidateObj],
      },
      labelData: {},
      newStatusOptions: [],
    };
  },
  computed: {},
  watch: {},
  mounted() {},
  methods: {
    submitDialogForm() {
      this.$refs['editStatusFormRef'].validate((valid) => {
        if (valid) {
          this.submitLoading = true;
          let params = {
            receiverIdentityId: this.labelData.identityId,
            targetStatus: this.editStatusForm.newStatus,
            remark: this.editStatusForm.remark,
            originalStatus: this.editStatusForm.currentStatus,
          };
          ediStatus(params).then((res) => {
            if (res.header && res.header.success) {
              this.editSuccess();
            } else {
              this.showEditStatusDialog = false;
            }
            this.submitLoading = false;
          });
        }
      });
    },
    editSuccess() {
      this.$emit('closeDialog', { type: 'status', value: this.editStatusForm.newStatus });
      this.showEditStatusDialog = false;
      this.$modal.msgSuccess(this.$t('IDC.till.editStatusY'));
      this.$emit('closeStatusDialog');
      this.$refs['editStatusFormRef'].resetFields();
    },
    showStatus(val) {
      switch (val) {
        case '03':
          return 'Active';
        case '05':
          return 'Frozen';
        default:
          return;
      }
    },
    closeEditLanguageDialog() {
      this.showEditStatusDialog = false;
      this.$refs['editStatusFormRef'].resetFields();
    },
    handleEditLanguage() {
      this.showEditStatusDialog = true;
    },
    showView(val) {
      this.showEditStatusDialog = true;
      this.labelData = val;
      this.editStatusForm.currentStatus = val.status;
      if (val.status == '05') {
        this.newStatusOptions = [
          {
            status: '03',
            value: 'Active',
          },
        ];
        this.editStatusForm.newStatus = '03';
      } else {
        this.newStatusOptions = [
          {
            status: '05',
            value: 'Frozen',
          },
        ];
        this.editStatusForm.newStatus = '05';
      }
    },
  },
};
</script>

<style scoped lang="scss">
.Remark-Reason {
  font-size: 16px;
  font-weight: 400;
  color: rgba(78, 89, 105, 1);
}
</style>
