import {
  nonSpecialCharValidateObj,
  inputRequiredValidateObj,
  selectRequiredValidateObj,
  integerValidateObj,
  len1To18StrValidateObj,
  len24StrValidateObj,
  nonSpecialCharExceptPercentSignValidateObj,
  checkLengthExceptPercentSign,
  msisdnValidateObj,
} from '@/common/validator/common';
import { checkTill, checkRangeDcc, defaultOperatorIdRule } from '../api/api-till';

let checNewkMsisdn = (rule, value, callback) => {
  if (value) {
    let params = {
      msisdn: value,
    };
    checkRangeDcc(params).then((res) => {
      if (res.flag) {
        callback();
      } else {
        callback(new Error(`${res.description || ''}`));
      }
    });
  }
};

let getSearchTillRules = () => {
  return {
    msisdn: [len1To18StrValidateObj],
    tillNumber: [len1To18StrValidateObj],
    orgshortcode: [integerValidateObj, len24StrValidateObj],
  };
};
let tillListRules = () => {
  return {
    msisdn: [
      inputRequiredValidateObj,
      nonSpecialCharExceptPercentSignValidateObj,
      len1To18StrValidateObj,
      msisdnValidateObj,
    ],
    tillNumber: [nonSpecialCharExceptPercentSignValidateObj, len1To18StrValidateObj],
    orgshortcode: [integerValidateObj, len24StrValidateObj],
    status: [selectRequiredValidateObj],
    productName: [selectRequiredValidateObj],
    queryCondition: [selectRequiredValidateObj],
  };
};
function findData(val, params) {
  let list = val.list;
  let validator = '';
  list.forEach((item) => {
    if (item.dataId === params) {
      if (item.data) {
        let data = JSON.parse(item.data).value;
        validator = data;
      }
    }
  });
  return validator;
}
let checkTillNumber = (rule, value, callback) => {
  if (value) {
    let params = {
      tillNumber: value,
    };
    checkTill(params).then((res) => {
      if (res.flag) {
        callback();
      } else {
        if (value.trim().length < 4 || value.trim().length > 12) {
          return callback(new Error('The length ranges from 4 to 12.'));
        }

        if (!new RegExp(/^([0-9A-Za-z]{4,12})$/).test(value)) {
          return callback(new Error('Special characters are not allowed. Please enter again.'));
        } else {
          callback();
        }
      }
    });
  }
};

let getCreateTillRules = () => {
  return {
    msisdn: [
      inputRequiredValidateObj,
      nonSpecialCharValidateObj,
      len1To18StrValidateObj,
      integerValidateObj,
      { validator: checNewkMsisdn, trigger: 'blur' },
    ],
    tillNumber: [inputRequiredValidateObj, nonSpecialCharValidateObj, { validator: checkTillNumber, trigger: 'blur' }],
    organizationShortCode: [selectRequiredValidateObj, inputRequiredValidateObj, nonSpecialCharValidateObj],
    languageCode: selectRequiredValidateObj,
    productIdList: [selectRequiredValidateObj],
  };
};
let getDetailMsisdnRules = () => {
  return {
    newMsisdn: [
      integerValidateObj,
      inputRequiredValidateObj,
      nonSpecialCharValidateObj,
      { validator: checNewkMsisdn, trigger: 'blur' },
    ],
    remark: [inputRequiredValidateObj],
  };
};
let getDetaliTillRules = () => {
  return {
    remark: [inputRequiredValidateObj, nonSpecialCharValidateObj],
  };
};
let getDefaultOperatorIDRules = async () => {
  let res = await defaultOperatorIdRule();
  if (res.header && res.header.success && res.result) {
    return [selectRequiredValidateObj];
  } else {
    return [];
  }
};

export {
  getSearchTillRules,
  tillListRules,
  getCreateTillRules,
  getDetaliTillRules,
  findData,
  getDetailMsisdnRules,
  checNewkMsisdn,
  getDefaultOperatorIDRules,
};
